@import 'reset';

$gray: #EFEFEF;
$accent: #66C05D;
$dark: #333333;

@font-face {
  font-family: 'Roboto';
  src: url('../fonts/Roboto-Regular.ttf');
}

body {
  max-width: 1920px;
  margin: 0 auto;
  font-family: Roboto, Arial, sans-serif;
}

.top-menu {
  height: 50px;
  background-color: $gray;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 15px;

  &-link {
    font-size: 12px;
    line-height: 16px;
    color: $dark
  }
}

.header {
  padding: 20px 10px;
  display: flex;
  justify-content: space-between;
  border-bottom: 1px solid $gray;

  .logo-container {
    position: relative;
    display: inline-flex;
    justify-content: space-around;
    align-items: center;

    .logo {
      width: 40px;
      height: 40px;
      margin-right: 5px;
    }

    .label {
      display: block;
      font-family: Roboto;
      font-size: 28px;
      font-weight: 700;
      line-height: 32px;
      letter-spacing: 0.1em;
      color: $accent;
      text-transform: uppercase;
    }

    .logo-link {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}

.contacts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 10px 0;

  .phone {
    display: block;
    font-size: 16px;
    line-height: 19px;
    color: #000;
  }

  .call-btn {
    background-color: #fff;
    border: 2px solid $accent;
    padding: 5px 10px;
  }
}

.btn {
  font-size: 14px;
  line-height: 16px;
  border-radius: 40px;
}

.main-menu {
  padding: 20px 0;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px 20px;

  &-link {
    font-size: 16px;
    line-height: 19px;
    color: #000;
  }
}

.sale {
  position: relative;

  .main-img {
    width: 100%;
    min-height: 220px;
    object-fit: cover;
  }

  div {
    position: absolute;
    top: 10%;
    left: 5%;
    bottom: 5px;
    max-width: 45%;

    h1 {
      font-size: 26px;
      font-weight: 700;
      color: #fff;
      margin-bottom: 15px;
    }

    p {
      font-size: 14px;
      line-height: 18px;
      color: #fff;
    }

    .green-link {
      display: inline-block;
      padding: 6px 12px;
      position: absolute;
      bottom: 5px;
      font-size: 14px;
      line-height: 16px;
      border-radius: 40px;
      color: #fff;
      border: 2px solid #FFFFFF;
    }
  }
}

.service {
  background-color: $gray;

  header {
    padding: 8px;
    display: flex;
    justify-content: space-around;
    align-items: center;

    h2 {
      font-weight: 700;
      font-size: 16px;
      line-height: 22px;
    }

    .car-svg {
      width: 30%;
      max-width: 80px;
    }
  }

  .products-container {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    padding-bottom: 20px;

    .product-card {
      display: flex;
      flex-direction: column;
      justify-content: space-evenly;
      align-items: center;
      width: 260px;
      min-height: 360px;
      background-color: #fff;

      .description {
        font-size: 14px;
        line-height: 20px;
        text-align: center;
        color: $dark;
      }

      .price {
        font-size: 21px;
        line-height: 25px;
        text-align: center;
        letter-spacing: 0.05em;
        color: #000;
        margin-bottom: 10px;
      }

      .buy-btn {
        background-color: $accent;
        color: #fff;
        padding: 10px 40px;
        margin-bottom: 10px;
      }
    }

    .product-card:not(:last-child) {
      margin-bottom: 20px;
    }
  }



}